import { createStyles, makeStyles, Typography } from '@material-ui/core';

type Props = {
    a: number;
    b: number;
}

const useStyles = makeStyles(theme => createStyles({
    equation: {

    },
    operand: {

    },
    operator: {
        margin: theme.spacing(1)
    },
    result: {

    }
}));

const Equation = (props: Props) => {
    const { a, b } = props;
    const classes = useStyles();

    return (
        <div className={classes.equation}>
            <Typography component="span" variant="h1" className={classes.operand}>{a}</Typography>
            <Typography component="span" variant="h1" className={classes.operator}>&times;</Typography>
            <Typography component="span" variant="h1" className={classes.operand}>{b}</Typography>
            <Typography component="span" variant="h1" className={classes.operator}>&#61;</Typography>
            <Typography component="span" variant="h1" className={classes.result}>{a * b}</Typography>
        </div>
    );
}

export default Equation;